import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import { t } from 'sn-translate';
import get from 'lodash/get';
import moment from 'moment';
import '@servicenow/now-button';
import '@servicenow/now-loader';
import '@servicenow/now-empty-state';
import 'sn-pa-target-list';
import 'sn-pa-target-form-connected';

import styles from './sn-pa-target-list-connected.scss';
import { TARGET_CLICKED, TAB_SELECTED } from 'sn-pa-target-list/constants';
import {
	EDIT_TARGET_CANCEL_CLICKED,
	CREATE_TARGET_CANCEL_CLICKED,
	UPDATE_TARGET_SUCCEEDED,
	DELETE_TARGET_SUCCEEDED,
	CREATE_TARGET_SUCCEEDED,
	EDIT_TARGET_BACK_CLICKED,
	CREATE_TARGET_BACK_CLICKED,
} from 'sn-pa-target-form-connected/constants';

import {
	TARGET_DATE_FORMAT,
	TARGET_DATE_VISIBLE_FORMAT,
	TARGET_DATE,
	TARGET_TYPE,
} from 'sn-pa-target-form/constants';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;
import { fetchTargetsList, canCreateGlobalTarget } from './actions';
import {
	isPastTarget,
	isCurrentTarget,
	isFutureTarget,
	parseQueryResponse,
	focusToNode,
	getBaselineTargetValue,
	getLastTargetValue,
} from './utils';
import {
	FETCH_LIST_REQUESTED,
	FETCH_LIST_STARTED,
	FETCH_LIST_SUCCEEDED,
	FETCH_LIST_FAILED,
	FETCH_STATUS_STARTED,
	FETCH_STATUS_SUCCESS,
	FETCH_STATUS_FAILED,
	TARGET_VALUE_EDIT_CLICKED,
	COMPONENT_ERROR,
	CREATE_TARGET_CLICKED,
	CAN_CREATE_GLOBAL_TARGET_REQUESTED,
	CAN_CREATE_GLOBAL_TARGET_SUCCEEDED,
	NAVIGATE_BACK_CLICKED,
} from './constants';

const handleCancel = ({
	properties: { manageCancelEdit, cancelEdit },
	updateState,
}) => {
	if (!manageCancelEdit || (manageCancelEdit && cancelEdit))
		updateState({
			triggerFocus: true,
			createTarget: false,
			selectedTargetValue: undefined,
			selectedTarget: undefined,
			selectedTargetStartDate: undefined,
		});
};

const fetchList = ({ dispatch, queryString }) => {
	dispatch(FETCH_LIST_REQUESTED, {
		queryString,
	});
	return true;
};

const getFormTitle = ({ targetType, listType }) => {
	if (listType === 'all') return t('Targets');

	return targetType === 'personal'
		? t('Personal Target')
		: t('Global Target');
};

const view = (
	{
		properties: {
			type,
			uuid,
			targetStatusMap,
			filters = [],
			averageScore,
			hideTabs,
			hideFilters,
			showIndicatorName,
			hasMultipleElements,
		},
		init,
		triggerFocus,
		prevFocusElm,
		items,
		targetType,
		createTarget,
		createTargetElm,
		errorMessage,
		fetchStatus,
		selectedItem,
		selectedTargetValue,
		selectedTargetStartDate,
		hasCreateTargetRole,
	},
	{ updateState }
) => {
	if (errorMessage)
		return (
			<now-empty-state
				type="not-configured"
				variant="small"
				headerText={errorMessage}
			/>
		);


	const isLoading = !items || fetchStatus === FETCH_STATUS_STARTED;

	const setElementRef = (ref, name) => updateState({ [name]: ref });

	if (init && createTargetElm) {
		focusToNode(createTargetElm);
		updateState({ init: false });
	}

	if (triggerFocus && prevFocusElm) {
		focusToNode(prevFocusElm);
		updateState({ triggerFocus: false, prevFocusElm: undefined });
	} else if (triggerFocus && !prevFocusElm) {
		focusToNode(createTargetElm.parentElement, 'sn-pa-target-list-item');
		updateState({ triggerFocus: false, prevFocusElm: undefined });
	}

	let baselineTargetValue;
	if (createTarget && Array.isArray(items)) {
		const targetItems = items.filter(
			(item) =>
				((!targetType || targetType === 'global') &&
					get(item, 'target.owner.sys_id') === null) ||
				(targetType === 'personal' &&
					get(item, 'target.owner.sys_id') !== null)
		);
		baselineTargetValue = selectedTargetStartDate
			? getBaselineTargetValue(
				targetItems,
				moment(
					selectedTargetStartDate,
					TARGET_DATE_VISIBLE_FORMAT
				).format(TARGET_DATE_FORMAT)
			)
			: getLastTargetValue(targetItems);
	}

	return (
		<Fragment>
			{selectedTargetValue || createTarget ? (
				<sn-pa-target-form-connected
					uuid={uuid}
					target-value={
						!createTarget ? selectedTargetValue : undefined
					}
					create-target={createTarget}
					form-title={
						!createTarget
							? getFormTitle({ targetType, listType: type })
							: undefined
					}
					target-type={targetType || 'global'}
					baseline-target-value={baselineTargetValue}
					average-score={averageScore}
					has-create-target-role={hasCreateTargetRole}
					show-back-button={true}
				>
					<slot slot="kpi-info" name="kpi-info" />
				</sn-pa-target-form-connected>
			) : undefined}
			<sn-pa-target-list
				hook-insert={({ elm }) => setElementRef(elm, 'targetListElm')}
				hook-destroy={() => setElementRef(null, 'targetListElm')}
				class={{ hidden: selectedTargetValue || createTarget }}
				type={type}
				hide-tabs={hideTabs}
				showIndicatorName={showIndicatorName}
				hasMultipleElements={hasMultipleElements}
				filters={
					!hideFilters
						? [
							{
								id: 'past',
								label: t('Past Targets'),
								fn: (a, index, list) =>
									isPastTarget(a, index, list),
							},
							{
								id: 'current',
								label: t('Current Target'),
								fn: (a, index, list) =>
									isCurrentTarget(
										a,
										index,
										list,
										index === 0
									),
							},
							{
								id: 'future',
								label: t('Future Targets'),
								fn: (a, index, list) =>
									isFutureTarget(a, index, list),
							},
							...filters,
						]
						: undefined
				}
				selected-item={selectedItem}
				target-status-map={targetStatusMap}
				items={items}
				has-create-target-role={hasCreateTargetRole}
			>
				{!hasMultipleElements ? (
					<now-button-iconic
						aria-config={{
							'aria-label': t('Create target'),
						}}
						hook-insert={({ elm }) =>
							setElementRef(elm, 'createTargetElm')
						}
						hook-destroy={() => setElementRef(null, 'createTargetElm')}
						append-to-payload={{ type: CREATE_TARGET_CLICKED }}
						variant="tertiary"
						size="md"
						slot="post-title"
						icon="plus-outline"
						tooltip-content={
							!hasCreateTargetRole &&
								(targetType === 'global' || !targetType)
								? t(
									'You do not have the appropriate role to create a global target.'
								)
								: t('Create Target')
						}
					/>
				) : ''}
				{isLoading ? (
					<now-loader
						slot="placeholder"
						label={t('Fetching targets')}
					/>
				) : undefined}
				{!hasMultipleElements ? (
					<div slot="pre-list">
						<slot name="kpi-info" />
					</div>
				) : ''}
			</sn-pa-target-list>
		</Fragment>
	);
};

/**
 *`sn-pa-target-list-connected` is an independent component that is designed to support
 * the features of Targets for workspaces. This component provides the capability to set 
 * targets for any KPI which helps the user to analyse the KPI performance in a much efficient manner.
 * 
 * ```
 * <sn-pa-target-list-connected
 *		query-string='target.ownerISEMPTY^ORtarget.ownerDYNAMIC90d1921e5f510100a9ad2572f2b477fe^ORDERBYDESCtarget_at'
 *		enable-edit=true
 * />
 *```
 * @seismicElement sn-pa-target-list-connected
 * @summary A container component to support Targets functionality for KPIs.
 */
createCustomElement('sn-pa-target-list-connected', {
	renderer: { type: snabbdom },
	initialState: {
		init: false,
	},
	properties: {
		/**
		 * `queryString` of target list connected.
		 * This property helps to fetch the list of targets.
		 * @type {string}
		 */
		queryString: {
			schema: {
				type: 'string',
			},
		},
		/**
		 * `uuid` of target list connected. 
		 * UUID in string format.
		 * @type {string}
		 */
		uuid: {
			schema: {
				type: 'string',
			},
		},
		/**
		 * `type` of target list connected.
		 * It helps user to set the visibility of the target type (Global/Personal).
		 * @type {string}
		 */
		type: {
			schema: {
				type: 'string',
				enum: ['global', 'personal', 'all'],
			},
		},
		/**
		 * `targetUnit` of target list connected.
		 * This property can be passed by the user to specify the target unit to be shown for targets on list.
		 * @type {string}
		 */
		targetUnit: {
			schema: {
				type: 'string',
			},
		},
		/**
		 * `hideTabs` of target list connected.
		 * When true, the tabs selector for target type is visually hidden.
		 * @type {boolean}
		 */
		hideTabs: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		/**
		 * `hideFilters` of target list connected.
		 * When true, the filters button for targets is visually hidden.
		 * @type {boolean}
		 */
		hideFilters: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		/**
		 * `showIndicatorName` of target list connected.
		 * If true, this property displays indicator name for all the targets in the list.
		 * @type {boolean}
		 */
		showIndicatorName: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		/**
		 * `hasMultipleElements` of target list connected.
		 * If true, this property signifies that multiple element filters has been selected for a breakdown.
		 * This property is passed from the consuming parent component.
		 * @type {boolean}
		 */
		hasMultipleElements: {
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * `averageScore` of target list connected.
		 * It represents the average score for target improvement.
		 * @type {integer}
		 */
		averageScore: {
			schema: {
				type: 'integer',
			},
		},
		/**
		 * `manageCancelEdit` of target list connected.
		 * If true, this property enables to override automatic updates to the `cancelEdit` property.
		 * @type {boolean}
		 */
		manageCancelEdit: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		/**
		 * `cancelEdit` of target list connected.
		 * If true, this property enables the user to navigate back to the list of targets.
		 * @type {boolean}
		 */
		cancelEdit: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		/**
		 * `isVisible` of target list connected.
		 * If true, this property enables focus to the first focusable element (`Create Target` button) in the target list view.
		 * @type {boolean}
		 */
		isVisible: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		/**
		 * `enableEdit` of target list connected.
		 * If true, this property enables edit on click of target value.
		 * @type {boolean}
		 */
		enableEdit: {
			schema: {
				type: 'boolean',
			},
			default: true,
		},
		/**
		 * `manageEdit` of target list connected.
		 * If true, this property enables the user to override automatic updates to the `edit` property.
		 * @type {boolean}
		 */
		manageEdit: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		/**
		 * `edit` of target list connected.
		 * If true, enables the user to navigate to the edit form for the target value being clicked.
		 * @type {boolean}
		 */
		edit: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		/**
		 * `FilterInfo`
		 * {
		 * 	id: string,
		 * 	label: string,
		 * 	fn: Function
		 * }
		 * @typedef {Object} FilterInfo
		 * @property {string} id - filter id
		 * @property {string} label - filter label to be displayed on list
		 * @property {Function} fn - filter function to be applied on list
		 * @type {FilterInfo[]} 
		 */
		filters: {
			schema: {
				type: 'array',
				properties: {
					id: {
						type: 'string',
					},
					label: {
						type: 'string',
					},
					fn: {},
				},
			},
			default: [],
		},
		/**
		 * `sortConfig` of target list connected.
		 * This property can be passed by users to apply sort functions config on the target list
		 * @type {Object}
		 */
		sortConfig: {
			schema: {
				type: 'object',
			},
		},
		/**
		 * `sortType` of target list connected.
		 * This property can be passed by users to apply sort function name on the target list
		 * @type {string}
		 */
		sortType: {
			schema: {
				type: 'string',
			},
		},
	},
	actions: {
		[FETCH_LIST_REQUESTED]: {
			private: true,
		},
		[FETCH_LIST_STARTED]: {
			private: false,
		},
		[FETCH_LIST_SUCCEEDED]: {
			private: false,
		},
		[FETCH_LIST_FAILED]: {
			private: false,
		},
	},
	actionHandlers: {
		/**
		 * Component init events
		 */
		[COMPONENT_BOOTSTRAPPED]: ({
			state: {
				properties: { queryString },
			},
			updateState,
			dispatch,
		}) => {
			if (queryString) {
				updateState({
					init: true,
				});
				dispatch(FETCH_LIST_REQUESTED, {
					queryString,
				});
				dispatch(CAN_CREATE_GLOBAL_TARGET_REQUESTED);
			} else {
				updateState({
					init: true,
					items: [],
				});
				dispatch(COMPONENT_ERROR, {
					message: t('Invalid Query string provided'),
				});
			}
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			action: { payload },
			properties: { manageCancelEdit },
			state: { createTargetElm },
			dispatch,
			updateState,
		}) => {
			const { name, previousValue, value } = payload;

			if (name === 'queryString' && previousValue !== value) {
				if (value) fetchList({ dispatch, queryString: value });
				else
					dispatch(COMPONENT_ERROR, {
						message: t('Invalid Query string provided'),
					});
			} else if (
				manageCancelEdit &&
				name === 'cancelEdit' &&
				value !== previousValue &&
				value === true
			) {
				updateState({
					createTarget: false,
					selectedTargetValue: undefined,
					selectedTarget: undefined,
				});
			} else if (
				name === 'isVisible' &&
				value !== previousValue &&
				value === true &&
				createTargetElm
			)
				focusToNode(createTargetElm);
		},
		/**
		 * List fetch actions
		 */
		[FETCH_LIST_REQUESTED]: fetchTargetsList,
		[FETCH_LIST_STARTED]: ({ updateState }) =>
			updateState({
				fetchStatus: FETCH_STATUS_STARTED,
				errorMessage: undefined,
			}),
		[FETCH_LIST_SUCCEEDED]: ({ action: { payload }, updateState }) => {
			const response = get(
				payload,
				'data.GlideRecord_Query.pa_target_values._results',
				{}
			);
			updateState({
				fetchStatus: FETCH_STATUS_SUCCESS,
				items: parseQueryResponse(response),
			});
		},
		[FETCH_LIST_FAILED]: ({ updateState, dispatch }) => {
			updateState({ fetchStatus: FETCH_STATUS_FAILED, items: [] });
			dispatch(COMPONENT_ERROR, { message: t('Fetching list failed') });
		},
		[CAN_CREATE_GLOBAL_TARGET_REQUESTED]: canCreateGlobalTarget,
		[CAN_CREATE_GLOBAL_TARGET_SUCCEEDED]: ({
			action: { payload },
			updateState,
		}) => {
			updateState({ hasCreateTargetRole: payload.result });
		},
		[COMPONENT_ERROR]: ({
			action: {
				payload: { message },
			},
			updateState,
		}) => {
			updateState({
				errorMessage: message,
			});
		},
		[TARGET_CLICKED]: ({
			action: {
				payload: { paTarget, paTargetValue, targetNode },
				stopPropagation,
			},
			properties: { type, enableEdit, manageEdit, edit },
			state: { selectedItem },
			dispatch,
			updateState,
		}) => {
			stopPropagation();
			dispatch(TARGET_VALUE_EDIT_CLICKED, {
				paTargetValue,
			});
			if (
				enableEdit !== false &&
				((manageEdit && edit) || !manageEdit) &&
				selectedItem !== paTargetValue.sys_id
			) {
				updateState({
					prevFocusElm: targetNode,
					selectedItem:
						type === 'all' ? paTargetValue.sys_id : undefined,
					selectedTarget: paTarget,
					selectedTargetValue: paTargetValue,
				});
			}
		},
		[NAVIGATE_BACK_CLICKED]: handleCancel,
		[EDIT_TARGET_CANCEL_CLICKED]: ({
			dispatch,
			updateState,
			state: { prevFocusElm },
		}) => {
			updateState({
				init: prevFocusElm ? false : true,
				triggerFocus: prevFocusElm ? true : false,
			});
			dispatch(NAVIGATE_BACK_CLICKED);
		},
		[CREATE_TARGET_CANCEL_CLICKED]: ({ dispatch, updateState }) => {
			updateState({ init: true });
			dispatch(NAVIGATE_BACK_CLICKED);
		},
		[EDIT_TARGET_BACK_CLICKED]: ({ dispatch }) =>
			dispatch(NAVIGATE_BACK_CLICKED),
		[CREATE_TARGET_BACK_CLICKED]: ({ dispatch }) =>
			dispatch(NAVIGATE_BACK_CLICKED),
		[CREATE_TARGET_SUCCEEDED]: ({
			dispatch,
			properties: { queryString },
		}) =>
			fetchList({ dispatch, queryString }) &&
			dispatch(NAVIGATE_BACK_CLICKED),
		[UPDATE_TARGET_SUCCEEDED]: ({
			dispatch,
			properties: { queryString },
		}) =>
			fetchList({ dispatch, queryString }) &&
			dispatch(NAVIGATE_BACK_CLICKED),
		[DELETE_TARGET_SUCCEEDED]: ({
			dispatch,
			properties: { queryString },
		}) =>
			fetchList({ dispatch, queryString }) &&
			dispatch(NAVIGATE_BACK_CLICKED),
		[TAB_SELECTED]: ({
			action: {
				payload: { value },
			},
			updateState,
		}) => updateState({ targetType: value }),
		'NOW_BUTTON_ICONIC#CLICKED': ({
			action: {
				payload: { type },
			},
			state: { createTargetElm, hasCreateTargetRole, targetType },
			updateState,
		}) => {
			if (type === CREATE_TARGET_CLICKED)
				updateState({
					prevFocusElm: createTargetElm,
					createTarget: true,
					selectedTargetValue: undefined,
				});
		},
		/**
		 * Handles the date value change from the sn-pa-target-form
		 */
		'NOW_RECORD_DATE_PICKER#VALUE_CHANGED': ({
			action: {
				payload: { value, type },
				stopPropagation,
			},
			updateState,
		}) => {
			if (type === TARGET_DATE) {
				updateState({ selectedTargetStartDate: value });
				stopPropagation();
			}
		},
		/**
		 * Handles the change between global and personal target creation
		 * from radio buttons on the target form
		 */
		[TARGET_TYPE]: ({ action: { payload }, updateState }) => {
			updateState({ targetType: payload.value });
		},
	},
	behaviors: [{ behavior: rtlBehavior }],
	view,
	styles,
	dispatches: {
		/**
		 * Dispatched on component bootstrap to fetch the list of targets if `queryString` is present.
		 * @actionType SN_PA_TARGET_LIST_CONNECTED#FETCH_LIST_REQUESTED
		 */
		'SN_PA_TARGET_LIST_CONNECTED#FETCH_LIST_REQUESTED': {},
		/**
		 * Dispatched when the user clicks on `cancel` or `back` button to return back to the list of targets from edit/create forms.
		 * @actionType SN_PA_TARGET_LIST_CONNECTED#NAVIGATE_BACK_CLICKED
		 */
		'SN_PA_TARGET_LIST_CONNECTED#NAVIGATE_BACK_CLICKED': {},
		/**
		 * Dispatched on component bootstrap to check for the user persmissions to have write access for global targets if `queryString` is present.
		 * @actionType SN_PA_TARGET_LIST_CONNECTED#CAN_CREATE_GLOBAL_TARGET_REQUESTED
		 */
		'SN_PA_TARGET_LIST_CONNECTED#CAN_CREATE_GLOBAL_TARGET_REQUESTED': {},
		/**
		 * Dispatched to handle error state with relevant error message. 
		 * @actionType SN_PA_TARGET_LIST_CONNECTED#ERROR_OCCURRED
		 */
		'SN_PA_TARGET_LIST_CONNECTED#ERROR_OCCURRED': {},

	}
});
