import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	FETCH_LIST_FAILED,
	FETCH_LIST_STARTED,
	FETCH_LIST_SUCCEEDED,
	CAN_CREATE_GLOBAL_TARGET_SUCCEEDED,
	CAN_CREATE_GLOBAL_TARGET_STARTED,
	CAN_CREATE_GLOBAL_TARGET_FAILED,
} from './constants';

export const targetsListQuery = `query ($queryString: String) {
	GlideRecord_Query {
	  pa_target_values(queryConditions: $queryString) {
		_results {
		  sys_id {
			  value
		  }
		  value {
			value
		  }
		  target_at {
			value
		  }
		  target_review {
			value
		  }
		  sys_domain {
			value
		  }
		  target {
			_reference {
			  active {
				value
			  }
			  indicator {
				_reference {
				  name {
					value
				  }
				  sys_id {
					value
				  }
				}
			  }
			  owner {
				_reference {
					sys_id {
						value
					}
				}
			  }
			  breakdown {
				_reference {
				  name {
					value
				  }
				  sys_id {
					value
				  }
				}
			  }
			  element {
				value
			  }
			  breakdown_level2 {
				_reference {
				  name {
					value
				  }
				  sys_id {
					value
				  }
				}
			  }
			  element_level2 {
				value
			  }
			  any_element {
				value
			  }
			  aggregate {
				value
			  }
			}
		  }
		}
	  }
	}
  }`;

export const fetchTargetsList = createGraphQLEffect(targetsListQuery, {
	variableList: ['queryString'],
	startActionType: FETCH_LIST_STARTED,
	successActionType: FETCH_LIST_SUCCEEDED,
	errorActionType: FETCH_LIST_FAILED,
});

export const canCreateGlobalTarget = createHttpEffect(
	'/api/now/pa/targets/can_create_global_target',
	{
		method: 'GET',
		startActionType: CAN_CREATE_GLOBAL_TARGET_STARTED,
		successActionType: CAN_CREATE_GLOBAL_TARGET_SUCCEEDED,
		errorActionType: CAN_CREATE_GLOBAL_TARGET_FAILED,
	}
);
