import { TARGET_DATE_FORMAT } from 'sn-pa-target-list/constants';
import moment from 'moment';
import { getDateValue } from 'sn-pa-target-list/src';
import cloneDeep from 'lodash/cloneDeep';

export function validUUIDProperty(uuid) {
	return typeof uuid === 'string' && !!uuid;
}

export function getCurrentTarget(list) {
	let foundCurrent = false;
	const isCurrentTarget = list.find((a) => {
		const isNotFuture =
			!foundCurrent &&
			moment(getDateValue(a.target_at), TARGET_DATE_FORMAT) <= moment();

		if (isNotFuture && !foundCurrent) foundCurrent = true;

		return foundCurrent && isNotFuture;
	});
	return isCurrentTarget;
}

let foundCurrent = false;
export const isCurrentTarget = (a, index, __, init = index === 0) => {
	if (init) foundCurrent = false;

	const isNotFuture =
		!foundCurrent &&
		moment(getDateValue(a.target_at), TARGET_DATE_FORMAT) <= moment();

	if (isNotFuture && !foundCurrent) foundCurrent = true;

	return foundCurrent && isNotFuture;
};

export const isFutureTarget = (a) => {
	return moment(a.target_at, TARGET_DATE_FORMAT) > moment();
};

export const isPastTarget = (a, index, list) => {
	return (
		!isCurrentTarget(a, index, list, index === 0) &&
		!isFutureTarget(a, index, list)
	);
};

export const parseQueryResponse = (data, init = true) => {
	let result;
	if (init) data = cloneDeep(data);
	if (data && typeof data === 'object')
		Object.keys(data).map((key) => {
			if (
				key === 'value' &&
				(typeof data[key] !== 'object' ||
					(typeof data[key] === 'object' && data[key] == undefined))
			) {
				result = data[key];
			} else if (key === '_reference' && data[key]) {
				data = { ...parseQueryResponse(data[key], false) };
			} else {
				data[key] = parseQueryResponse(data[key], false);
			}
		});
	return result !== undefined ? result : data;
};

export function focusToNode(node, query) {
	let focusNode = node;

	if (query) focusNode = (node.shadowRoot || node).querySelector(query);

	if (focusNode && typeof focusNode.focus === 'function')
		setTimeout(() => focusNode.focus(), 0);
}

/**
 * Calculates the closest existing target from a particular date
 * @param {Array} items
 * @param {String} selectedDate having format YYYYMMDD
 *
 * @returns Int target value
 */
export const getBaselineTargetValue = (items, selectedDate) => {
	let closestTarget;
	if (
		Array.isArray(items) &&
		moment(selectedDate, TARGET_DATE_FORMAT).isValid()
	) {
		for (let i in items)
			if (items[i].target_at <= selectedDate)
				closestTarget =
					!closestTarget ||
					closestTarget.target_at < items[i].target_at
						? items[i]
						: closestTarget;
	}
	return closestTarget ? closestTarget.value : undefined;
};

/**
 * Returns the target value of the most recent target set
 * @param {Array} items
 *
 * @returns Int target value
 */
export const getLastTargetValue = (items) => {
	let targetRecord;
	for (let i in items)
		if (!targetRecord || targetRecord.target_at < items[i].target_at)
			targetRecord = items[i];

	return targetRecord ? targetRecord.value : undefined;
};
